.header {
  width: 100%;
  z-index: 50;
  background-color: rgba(13, 13, 17, .8);
  padding: 32px 0;
  position: fixed;
  top: 0;
  left: 0;
}

.header-box {
  align-items: center;
  display: flex;
}

.header-logo {
  padding-right: 10px;
}

.menu {
  flex: auto;
  padding-right: 55px;
}

.menu-list {
  justify-content: flex-end;
  column-gap: 35px;
  display: flex;
}

.menu-link {
  color: #fff;
  font-size: 16px;
  font-weight: 300;
  line-height: 160%;
  position: relative;
}

.menu-link:before {
  content: "";
  width: 0;
  height: 1px;
  background-color: rgba(154, 154, 154, .34);
  transition: width .5s;
  display: block;
  position: absolute;
  bottom: -3px;
  right: 0;
}

.menu-link:hover:before {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #6ba559;
  transition: width .5s;
  display: block;
  position: absolute;
  bottom: -3px;
  left: 0;
}

.header-basket {
  letter-spacing: .96px;
  align-items: center;
  column-gap: 20px;
  padding-right: 55px;
  font-size: 17.6px;
  font-weight: 300;
  line-height: 160%;
  display: flex;
}

.number {
  position: relative;
}

.number:before {
  content: "";
  width: 30px;
  height: 30px;
  background: rgba(129, 129, 139, .3);
  border-radius: 50%;
  position: absolute;
  top: -1px;
  right: -9px;
}

.button-block {
  flex-shrink: 0;
}

.scroll-top {
  width: 54px;
  height: 54px;
  z-index: 9999;
  background: #4f2ee8;
  border: 5px solid #c3b9f1;
  border-radius: 50%;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.scroll-top-svg {
  width: 18px;
  height: 18px;
  fill: #fff;
}

.scroll-top.show {
  justify-content: center;
  align-items: center;
  display: flex;
}

.scrollbar {
  scrollbar-width: thin;
  scrollbar-color: #888 #f1f1f1;
}

.scrollbar::-webkit-scrollbar {
  width: 8px;
}

.scrollbar::-webkit-scrollbar-track {
  background-color: rgba(0, 0, 0, 0);
}

.scrollbar::-webkit-scrollbar-thumb {
  max-height: 30vh;
  background-color: #e0e0e0;
  border-radius: 12px;
}

.scrollbar::-webkit-scrollbar-thumb:hover {
  background-color: #555;
}

:root {
  --menu-color: #fff;
  --btn-color: #3d96f4;
}

